import "./styles.css";

var dFreccia;

function change() {
  dFreccia = "&rarr;";
  var m = Math.random();
  if (m > 0.5) {
    dFreccia = "&larr;";
  }
  var codice =
    `<h1>Aeroporto X</h1> 
                <div>Si prega di andare in questa direzione</div>
                <div>` +
    dFreccia +
    `</div>`;

  document.getElementById("app").innerHTML = codice;
  console.log(m);
}

function main() {
  change();
  document.querySelector("body").addEventListener("click", change);
}

main();
